.service{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 100vh;
    margin-top: 7rem;
    margin-bottom: 8rem;
    
 
   

}
.s-left{
    display: flex;
    flex-direction: column;
     position: relative;
   
}
.s-left>:nth-child(1)
{
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.s-left>:nth-child(2)
{
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.s-left>:nth-child(3)
{
    color: var(--gray);
    margin-top: 0.5rem;
    
}
.s-button{
    height: 2rem;
    width: 8rem;
    margin-top: 1rem;
}
.s-right{
    position: relative;

}
.s-blur2{
    left: -8rem;
    top: 48rem;
    background: #e6d197;
}
@media screen and (max-width:480px) {
    .service{
        display: flex;
      margin-top: 0;
     
      gap: 5rem;
      height: 65rem;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
  flex-direction: column;
  position: static;
    
     
    }
    .s-right{
        display: flex;
      flex-direction: column;
      gap: 17rem;
     
        /* align-items: center;
        justify-content: center; */
        
    }
    .one{
        align-items: center;
        justify-content: center;
        margin-top: 10px;
        margin-left: 36rem;
        display: flex;
        gap:15rem;
    }
    .s-right>*{
   
      position: static;
      transform: scale(0.8);
   
    }
    .card-wrapper{
       display:inline-block;
    }
  }