
:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);    
}
.button{
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 15px;
  padding: 10px 26px 10px 26px;
  background: linear-gradient(180deg,#fdc50f 26.75%, #fd982f 99.50%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161,40,0.45);
}
.button:hover{
  background:var(--black);
  cursor: pointer;
  color: var(--orange);
  border: 1px solid var(--orange);

}
.blur{
  position: absolute;
  width: 20rem;
  height: 13rem;
  border-radius: 50%;
  /* background: #232224;    #edd0ff */
  filter: blur(72px);
  z-index:-9;
  top: -18%;
  left: 56%;
}
.progress
{
  margin-left: 290px;

  text-decoration-color: aliceblue;
}
@media screen and (max-width:480px) {
  .App{
    padding: 0.5rem 1rem;
  }
}
  

  